<template>
<div>
    <h3>Write your own blog!</h3>
</div>

<div class="create-container">
    <div class="form-wrapper">
        <label for="blog-name">Blog name</label>
        <div class="input-with-button">
            <input v-model="blogName" type="text" class="title-form" id="blog-name" placeholder="write..." autocomplete="False" tab-index="0" aria-label="Title" aria-disabled="False" data-initial-value>
        
            <div class="dog-username-publish-wrapper">
                <div class="form-control dog">@</div>
                <input v-model="username" type="text" class="username-form" id="author" placeholder="author" aria-label="Username" aria-describedby="basic-addon1">
                <button id="publish-btn" @click="publish()"><img src="@/assets/send-icon.png" alt="Publish Icon" class="btn-icon"/>Publish</button>
            </div>
        </div>

        <div class="upload-img">
            <label for="blog-img" class="blog-img">Choose the main blog picture:</label><br/>
            <input type="file" accept="image/png, image/jpeg" @change="upload" id="blog-img"/>
        </div>
    </div>
    <label for="blog-content" class="blog-content">Blog content</label>
    <div class="form-bottom"></div>
</div>

<div class="blog-content-container">
    <div class="blog-text">
        <textarea v-model="blogContent" id="blog-content" rows="17" placeholder="your blog starts here..."></textarea>
    </div>
</div>
</template>

<script>
import blog from '@/blog.js';
import store from '@/store';

export default {
    name: 'CreateComponent',
    data() {
        return {
            블로그글: blog,
            username: "",
            blogName: "",
            blogContent: "",
            blogImg: "",
            newUpload: true,
        };
    },
    props: {},
    methods: {
        // upload main image for blog
        upload(e) {
            let 파일 = e.target.files;
            // console.log(파일[0]);
            let url = URL.createObjectURL(파일[0]);
            // console.log(url); 
            this.blogImg = url;
        },
        // publish new post
        publish() {
            if (!this.username || !this.blogName || !this.blogContent) {
                alert("Please fill in all the fields");
            return; }
            if (!this.blogImg) {
                alert("Please upload a blog image");
                return; }

            //this.blogContent = this.blogContent.replace(/\n/g, "\\n");

            var myBlog = {
                author: this.username,
                title: this.blogName,
                content: this.blogContent,
                image: this.blogImg,
                date: "December 31, 2023",
                number: "0",
                newUpload: this.newUpload,
            };

            // Using Vuex to update the state
            store.commit("addBlog", myBlog);

            // Clear the form fields after publishing
            this.username = "";
            this.blogName = "";
            this.blogContent = "";
            this.blogImg = "";

            // Redirect to ListComponent
            this.$router.push("/list");
        },
        
    },
};
</script>

