<!--
Bootstrap library를 npm으로 설치하기:
1) npm install bootstrap@5 (5버전 설치시) 또는 npm install bootstrap@4 jquery popper.js (4버전 설치시)
2) main.js: 
import 'bootstrap'
import 'bootstrap/dist/css/bootstrap.min.css'

Vue-router ver.4 설치하기: (페이지 나눌 때)
1) npm install vue-router@4
2) router.js
3) main.js:
import router from './router'
createApp(App).use(router).mount('#app')

Vuex 설치하기
1) npm install vuex@next --save
2) store.js
3) main.js:
import store from './store.js'
App.use(store).mount('#app')
-->

<template>
  <!-- bootstrap nav.bar -->
  <nav class="navbar navbar-expand-lg mb-0" style="background-color: rgb(35, 35, 35);">
    <div class="container-fluid">
      <a class="navbar-brand text-light" style="font-weight: bold;" href="#"><img src="./assets/logo-pink.png" width="35" height="30" class="d-inline-block align-top" alt=""> iblog</a>
      <button class="navbar-toggler navbar-dark" type="button" data-bs-toggle="collapse" data-bs-target="#navbarSupportedContent" aria-controls="navbarSupportedContent" aria-expanded="false" aria-label="Toggle navigation">
          <span class="navbar-toggler-icon text-light"></span>
      </button>
      <div class="collapse navbar-collapse" id="navbarSupportedContent">
        <ul class="navbar-nav me-auto mb-2 mb-lg-0">
          <li class="nav-item">
            <router-link to="/home" class="nav-link active text-light" aria-current="page">Home</router-link>
          </li>
          <li class="nav-item">
            <router-link to="/create" class="nav-link active text-light" aria-current="page">New Blog</router-link>
          </li>
          <li class="nav-item">
            <router-link to="/list" class="nav-link active text-light" aria-current="page">Blog Directory</router-link>
          </li>
          <li class="nav-item dropdown">
            <a class="nav-link dropdown-toggle text-light" href="#" role="button" data-bs-toggle="dropdown" aria-expanded="false">
              Dropdown
            </a>
            <ul class="dropdown-menu" style="background-color: rgb(50, 50, 50);">
              <li><a class="dropdown-item text-light" href="#">Action</a></li>
              <li><a class="dropdown-item text-light" href="#">Another action</a></li>
              <li><hr class="dropdown-divider"></li>
              <li><a class="dropdown-item text-light" href="#">Something else here</a></li>
            </ul>
          </li>
        </ul>
        <div class="d-flex" role="search">
          <input v-model="search_keyword" class="form-control me-2" id="searched-keyword" type="text" placeholder="Search by title">
          <button @click="search_by_title(search_keyword)" id="search-btn" aria-current="page">Search</button>
        </div>
      </div>
    </div>
  </nav>

  <!-- example: <router-link to="/home">홈 페이지</router-link> <br> -->

  <!--<ListComponent :블로그="블로그들[i]" v-for="(b,i) in 블로그들" :key="i" />-->
  <!-- use router instead : /list or /home etc -->
  <div class="mt-4">
    <router-view :블로그글="블로그글" :검색블로그="검색블로그"></router-view>
  </div>
  
  <div class="home-bg" v-if="isFirstPage">
    <div class="slogan">
      <p class="slogan-big">Write about your hobbies</p>
      <h3>Creating your own blog takes just a few minutes.</h3>
    </div>
    <router-link id="start-now-btn" to="/create" >Start Now</router-link>
  </div>

  <div class="footer">
    <p>&copy; <a href="#">iblog</a>. All rights reserved.</p>
  </div>

</template>

<script>
// import ListComponent from './components/ListComponent.vue';  //used router instead
import blog from './blog.js';
import store from '@/store';

export default {
  name: 'App',
  data() {
    return {
      블로그글 : blog,
      검색블로그 : [],
      search_keyword : '',
    }
  },
  computed: {
    //check if the current route is the first page's route
    isFirstPage() {
      return this.$route.path === '/';
    },
  },
  components: {},
  methods: {
    search_by_title(search_keyword) {
      if(this.search_keyword != '') {
        console.log('App.vue, searching(before): ' + store.state.searching); 
        store.commit('isSearching');
        console.log('App.vue, searching(after): ' + store.state.searching); 
        
        this.검색블로그 = [];  //clear search array for new search

        for(let i = 0; i < this.블로그글.length; i++) {
          if(this.블로그글[i].title.toLowerCase().includes(this.search_keyword.toLowerCase())) {
            this.검색블로그.push(this.블로그글[i]);
          }
        }
        console.log('App.vue, search_keyword: ' + this.search_keyword); 
        this.search_keyword = '';
        this.$router.push('/list/' + search_keyword);
      }
      else {
        alert("Enter the search keyword");
      }
    },
  },
}
</script>

