<template>
<div class="list-intro-container">
    <div class="list-bg">
        <p class="list-intro1">Blog Directory</p>
        <p v-if="this.allTitles == true" class="list-intro2">View all blogs listed</p>
        <p v-if="this.searchedTitles == true" class="list-intro2">Searching blogs by title</p>
    </div>
    <div class="list-bg-wave"></div>
</div>
<div v-if="this.allTitles == true" class="list-container">
    <div class="preview-container" v-for="(blog, i) in 블로그글" :key="i">
        <!-- not a search > show all blogs -->
        <div class="preview-card">
            <div v-if="!블로그글[i].newUpload" class="preview-img" :style="{ backgroundImage: `url('${require('@/assets/' + 블로그글[i].image)}')` }"></div>
            <div v-if="블로그글[i].newUpload" class="preview-img" :style="{ backgroundImage: `url('${블로그글[i].image}')` }"></div>
            <p class="preview-title">{{ 블로그글[i].title }}</p>
            <p class="preview-date">{{ 블로그글[i].date }}, by {{ 블로그글[i].author }}</p>
            <p>{{ truncateContent(블로그글[i].content) }}</p>
            <p @click="$router.push('/detail/' + i)" id="read-more-btn">Read More →</p>
        </div>
    </div>
</div>

<div v-if="this.searchedTitles == true" class="list-container">
    <div class="preview-container" v-for="(blog, i) in 검색블로그" :key="i">
        <!-- search by title -->
        <div class="preview-card">
            <div v-if="!검색블로그[i].newUpload" class="preview-img" :style="{ backgroundImage: `url('${require('@/assets/' + 검색블로그[i].image)}')` }"></div>
            <div v-if="검색블로그[i].newUpload" class="preview-img" :style="{ backgroundImage: `url('${검색블로그[i].image}')` }"></div>
            <p class="preview-title">{{ 검색블로그[i].title }}</p>
            <p class="preview-date">{{ 검색블로그[i].date }}, by {{ 검색블로그[i].author }}</p>
            <p>{{ truncateContent(검색블로그[i].content) }}</p>
            <p @click="$router.push('/detail/' + i)" id="read-more-btn">Read More →</p>
            <div>{{ iteratedAll(i) }}</div>
        </div>
    </div>
</div>

</template>
  
<script>
import store from '@/store';

export default {
    name: 'ListComponent',
    data() {
        return {
            searchedTitles: false,
            allTitles: true,
            hasCheckedSearching: false,
        }
    },
    props: {
      블로그글: Array,
      검색블로그: Array,
    },
    beforeMount() {
    // Run only once before the component's code runs
    if (!this.hasCheckedSearching) {
      this.checkSearching();
      this.hasCheckedSearching = true; // Set the flag to true
    }
    },
    watch: {
    $route(to, from) {
        if(to.path !== from.path) {
            this.checkSearching();
        }
    }
    },
    methods: {
        truncateContent(content) {
            const maxLength = 150;
            if (content.length <= maxLength) {
                return content;
            } else {
                return content.slice(0, maxLength) + '...';
            }  
        },
        iteratedAll(i) {
            //iterated last searched title
            if(i == (this.검색블로그.length - 1)) {
                //this.allTitles = true;
                //this.searchedTitles = false;
                //store.commit('isSearching');  //true -> checkSearching again doesn't run again
                console.log('iteratedAll. allTitles=' + this.allTitles + ', searchedTitles=' + this.searchedTitles);
                console.log('iteratedAll: i=' + i + ', searching: ' + store.state.searching);
                return;
            }
        },
        checkSearching() {
            //not searching, show all titles
            if(store.state.searching == false) {
                this.allTitles = true;
                this.searchedTitles = false;
                console.log('checkSearching. allTitles=' + this.allTitles + ', searchedTitles=' + this.searchedTitles);
                console.log('checkSearching: Not searching. All titles. searching: ' + store.state.searching); 
            }
            //searching, show searched titles
            else if(store.state.searching == true) {
                this.allTitles = false;
                this.searchedTitles = true;
                //console.log(this.검색블로그);
                console.log('checkSearching. allTitles=' + this.allTitles + ', searchedTitles=' + this.searchedTitles);
                console.log('checkSearching: Some titles. searching: ' + store.state.searching); 
                store.commit('isSearching');  //change to not searching
                console.log('checkSearching: Some titles. searching: ' + store.state.searching); 
            }
        },
    },    

};
</script>
  
<style>
.list-intro-container {
    margin-bottom: 30px;
}

.list-bg {
    margin-top: -25px;
    background-color: rgb(254, 235, 248);
    height: 110px;
    display: flex;
    flex-direction: column;   /* Center children horizontally */
    align-items: center;      /* Center children horizontally */
    justify-content: center;  /* Center children vertically */

}

.list-bg-wave {
    /* margin-top: -25px; */
    background-image: url('@/assets/list-bg.jpg');
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;
    width: 100%;
    height: 90px;   /* 100% of the viewport height */
    /* color: #black;   Set text color to be visible against the background */
}

.list-intro1 {
    margin-top: 70px;
    font-size: 45px;
    font-weight: bolder;
    /* border-bottom: solid 1px;
    border-color: rgb(190, 66, 147); */
}
.list-intro2 {
    margin-top: -10px;
    font-size: 20px;
    font-weight: lighter;
    /* border-bottom: solid 1px;
    border-color: rgb(190, 66, 147); */
}

.list-container {
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(300px, 1fr));   /* define # of blogs in a line */
    gap: 25px;   /* distance b/w blogs in a line */
    margin: 0 60px 50px 60px;
    background-color: rgb(255, 255, 255);
}
  
.preview-container {
    box-shadow: 0 0px 8px rgba(0, 0, 0, 0.1);
    border-radius: 1px; 
    background-color: rgb(245, 245, 245);
}

.preview-card {
    margin: 10px;
    text-align: left;
}

.preview-img {
    width: 100%;
    height: 200px;
    background-size: cover;
    background-position: center;
    border-radius: 1px;
}
  
.preview-date {
    margin-top: 10px;
    color: rgb(97, 97, 97);
    font-size: 13px;
}

.preview-title {
    margin-top: 12px;
    font-weight: bold;
    font-size: 23px;
}
  
#read-more-btn {
    display: flex;
    align-items: center;
    margin-top: 30px;
    width: 102px;
    cursor: pointer;
    font-size: 15px;
    font-weight: bold;
    color: rgb(178, 58, 136);
}
</style>
  