<template>
    <div class="detail-containter">
        <div class="d-card">
            <h1>{{ 블로그글[$route.params.id].title }}</h1>
            <div class="d-meta-info">
                <p><i class="far fa-calendar-alt"></i> {{ 블로그글[$route.params.id].date }}</p>
                <p><i class="far fa-user"></i> {{ 블로그글[$route.params.id].author }}</p>
            </div>

            <div class="d-img" v-if="!블로그글[$route.params.id].newUpload" :style="{ backgroundImage: `url('${require('@/assets/' + 블로그글[$route.params.id].image)}')`, height: imageHeight }"></div>
            <div class="d-img" v-if="블로그글[$route.params.id].newUpload" :style="{ backgroundImage: `url('${블로그글[$route.params.id].image}')`, height: imageHeight }"></div>
            
            <!-- Button to toggle image height -->
            <div class="toggle-btn-container">
                <button class="toggle-btn" @click="toggleImageHeight">
                    {{ isFullHeight ? "[fold an image]" : "[open full image]" }}
                </button>
            </div>

            <p class="d-content">{{ 블로그글[$route.params.id].content }}</p>

            <router-view></router-view>

            <!-- nested routes: /detail/0/author : show 'children' from router.js
                $route.params.id : router's parameter
                $router.go(-1) : 뒤페이지로 이동 등 (go(1), go(-2))
                $route.fullPath : full path -->
        </div>
        <div class="navigation-buttons">
            <p v-if="$route.params.id != 0" @click="readPrevious($route.params.id);" id="previous-btn">Previous</p>
            <p v-if="$route.params.id != (블로그글.length - 1)" @click="readNext($route.params.id);" id="next-btn">Next</p>
        </div>
    </div>
</template>

<script>
    export default {
        name: 'DetailComponent',
        data() {
            return {
                isFullHeight: false,
            };
        },
        props: {
            블로그글 : Array,
        },
        computed: {
            imageHeight() {
                return this.isFullHeight ? '100vh' : '400px';
            },
        },
        methods: {
            readNext(id) {
                id++;
                return this.$router.push('/detail/' + id);
            },
            readPrevious(id) {
                id--;
                return this.$router.push('/detail/' + id);
            },
            toggleImageHeight() {
                this.isFullHeight = !this.isFullHeight;
            },
        },
    }
</script>

<style>
.detail-containter {
    max-width: 800px;
    margin: auto;
    margin-bottom: 50px;
}

.d-card {
    padding: 20px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
    background-color: #fff;
    border-radius: 8px;
}

h1 {
    font-size: 28px;
    margin: 10px 0 25px 0;
}

.d-meta-info {
    display: flex;
    justify-content: space-between;
    color: #7e7e7e;
    margin-bottom: 20px;
}

.d-meta-info p {
    margin: 0;
}

.d-img-masked {
    width: 100%;
    height: 400px;
    background-size: cover;
    background-position: center;
    border-radius: 8px;
    margin-bottom: 20px;
    -webkit-mask-image: -webkit-gradient(linear, left top, left bottom, from(rgba(0,0,0,1)), to(rgba(0,0,0,0)));
    mask-image: linear-gradient(to bottom, rgba(0,0,0,1), rgba(0,0,0,0));
}

.d-img {
    width: 100%;
    background-size: cover;
    background-position: center;
    border-radius: 8px;
}

.d-content {
    margin-top: 5px;
    text-align: left;
    font-size: 17px;
    line-height: 1.6;
    white-space: pre-wrap;
}

.navigation-buttons {
    margin: 15px;
    display: flex;
    justify-content: space-between;   /* position buttons on left and right */
    margin-top: 20px;
}

#previous-btn {
    cursor: pointer;
    color: rgb(178, 58, 136);
    font-size: 17px;
}

#next-btn {
    cursor: pointer;
    color: rgb(178, 58, 136);
    font-size: 17px;
}

#previous-btn:hover,
#next-btn:hover {
    text-decoration: underline;
}

.toggle-btn-container {
  display: flex;
  justify-content: flex-end;   /* align to the right */
}

.toggle-btn {
  background-color: #fff;
  border: none;
  cursor: pointer;
  padding: 10px;
  font-size: 15px;
  color: rgb(114, 114, 114);
}
</style>