// 모든 컴퍼먼트가 공유하는 공간
// For using Vuex

import { createStore } from "vuex";
import blog from '@/blog.js';

const store = createStore({
    state () {
        return {
            // 모든 컴퍼먼트가 공유하는 데이터: 쓰기, 변경 가능
            블로그글: blog,
            searching: false,
            //title_keyword: '',
        }
    },
    // functions to change state(값) of data
    // can be used by any .vue using $store.commit('func', data)
    mutations: {
        addBlog(state, myBlog) {
            state.블로그글.unshift(myBlog);
        },
        isSearching(state) {
            if(state.searching == true) {
                state.searching = false;
            }
            else { 
                state.searching = true; 
            }
        },
    },
    actions: {},
})

export default store;