import { createWebHashHistory, createRouter } from "vue-router";
// history: createWebHashHistory() is Hash Mode ( URL에 /#/ 붙은 채로 시작 )

import ListComponent from './components/ListComponent.vue';
import HomeComponent from './components/HomeComponent.vue';
import DetailComponent from './components/DetailComponent.vue';
import NoPageComponent from './components/NoPageComponent.vue';
import AuthorComponent from './components/AuthorComponent.vue';
import CommentsComponent from './components/CommentsComponent.vue';
import CreateComponent from './components/CreateComponent.vue';

const routes = [
  {
    path: "/list",  //url에서 가장 마지막에
    component: ListComponent,
    /* 여러 개 component 한번에 보여주기 (need <router-view> ?)
    components: {
      ListComponent,
      CommentsComponents,
      ...}*/
  },
  {
    path: "/list/:search_keyword",
    component: ListComponent,
  },
  {
    path: "/home",
    component: HomeComponent,
  },
  {
    path: "/create",
    component: CreateComponent,
  },
  {
    path: "/detail/:id",   // [/:id means 아무문자 instead of id], [/:id(\\d+) is 아무숫자], [:id* is 반복 (i.e. :id/:id/:id/...)]
    component: DetailComponent,
    children: [   //for nested routes : /detail/0/author
        {
            path: "author",
            component: AuthorComponent,
        },
        {
            path: "comments",
            component: CommentsComponent,
        },
    ],
  },
  {
    path: "/:anything(.*)",  //모든 문자 (put here, at the bottom ~ execute top->bottom)
    component: NoPageComponent,
  },
];

const router = createRouter({
  history: createWebHashHistory(),
  routes,
});

export default router; 